* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;

  list-style: none;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 10px;
  margin-left: 10px;
}

p {
  margin-top: 5;
  margin-bottom: 5;
}

.country-list {
  
  padding-left: 0px;
  margin-left: 10px;
}

.country-card {
  display: flex;
  align-items: center;
}

.item {
  display: flex;
  align-items: center;
}

.flag-icon {
margin-right: 10px;
}

